import React, {Component} from 'react';
import ImageInteractive from './ImageInteractive';
import Airtable from 'airtable';

Airtable.configure({endpointUrl: 'https://api.airtable.com', apiKey: 'keyuFtFTBZMHrzjOn'});

let prob_data;

class App extends Component {
   constructor() {
      super();
      this.state = {data : null};
   }
   compareFunc(a, b) {
      if (a.order > b.order) return 1;
      if (b.order > a.order) return -1;
      return 0;
   }
   componentDidMount() {
     prob_data = {
         name : 'Comparing Two Fractions',
         media : 'images-interactive',
         audio_abbrev : 'cotf_narration',
         img_abbrev : 'sbs_cotf',
         steps : []
     };
     let errors = [
         {'default' : "<video src='./modal_howto.mp4' playsInline loop uk-video='true'></video>"},
         {'default' : "<video src='./modal_howto.mp4' playsInline loop uk-video='true'></video>"},
         {'lesser-denom' : 'You wrote the lesser denominator. Write the greater denominator instead.', 'greater-denom' : 'You wrote the greater denominator. Write the lesser denominator instead.', 'lesser-num' : 'You wrote the greater numerator. Write the lesser numerator instead.', 'greater-num' : 'You wrote the lesser numerator. Write the greater numerator instead.', 'fraction' : 'Read the instructions. You don\'t need to write a fraction yet.', 'default' : "<video src='./modal_howto.mp4' playsInline loop uk-video='true'></video>"},
         {'non-fraction' : 'Read the directions. You need to write a fraction here.', 'larger-fraction' : 'You wrote the larger fraction. Write the smaller fraction instead.', 'smaller-fraction' : 'You wrote the smaller fraction. Write the larger fraction instead.', 'default' : "<video src='./modal_howto.mp4' playsInline loop uk-video='true'></video>"},
         {'non-fraction' : 'Read the directions. You need to write a fraction here.', 'larger-fraction' : 'You wrote the larger fraction. Write the smaller fraction instead.', 'smaller-fraction' : 'You wrote the smaller fraction. Write the larger fraction instead.', 'default' : "<video src='./modal_howto.mp4' playsInline loop uk-video='true'></video>"},
         {'default' : 'The inequality should be written as "smaller fraction &lt; larger fraction" or "larger fraction &gt; smaller fraction."'}
     ];
     let sorted_data = [];
     fetch('https://api.airtable.com/v0/appbJvIOkBMSrEz8z/Comparing%20Two%20Fractions?api_key=keyuFtFTBZMHrzjOn')
     .then((resp) => resp.json())
     .then(data => {
      for (var a = 0; a < data.records.length; a++) {
      data.records[a].fields.errors = errors[data.records[a].fields.order - 1];
      if (data.records[a].fields.step === 'reflectQ') prob_data.reflectQ = data.records[a].fields.text;
      else if (data.records[a].fields.step === 'reflect_suganswer') prob_data.reflect_suganswer = data.records[a].fields.text;
      else if (data.records[a].fields.step === 'testimgs') prob_data.testimgs = data.records[a].fields.Attachments
      else sorted_data.push(data.records[a].fields);
      }
      sorted_data = sorted_data.sort(this.compareFunc);
      sorted_data.forEach(function(record){prob_data.steps.push(record);});
      this.setState({data : prob_data});
     })
     .catch(err => {});
   }
   render() {
      if (!this.state.data || this.state.data === undefined || this.state.data === null) return null;
      let primary = '#4499cc';
      let secondary = '#557722';
      const disabled = '#D3D3D3';
      let dislabel = '#aaccdd';

      let scala;
      if (this.props.media === 'images-interactive') {
         scala = <ImageInteractive primcolor={primary} seccolor={secondary} discolor={disabled} dislabel={dislabel} probdata={this.state.data} />
      }
      return (
      <div>
      {scala}
      </div>
      );
   }
}

export default App;
